main {
  width: 84vw;
  padding-left: 10px;
  background-color: #f7f6f6;
  overflow-y: auto;
}
.main-container-sidebar {
  display: flex;
  justify-content: center;
  min-height: 100vh;
}
.content-container {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.sidebar-container {
  width: 20%;
  height: 100vh;
  background-color: #fff;
}

.sidebar-logo {
  width: 55%;
  margin: 10px 20% 20px 20%;
  object-fit: contain;
}

.sidebar-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 5px 0px 5px 25px;
  margin-bottom: 20px;
  text-decoration: none;
  height: 40px;
  color: black;
}

.sidebar-label {
  margin-left: 30px;
}

.sidebar-icon img {
  width: 30px;
  height: 30px;
}

.active {
  background: darkslateblue;
  color: #fff;
  padding: 5px 0px 5px 25px;
  border-radius: 4px;
}

.active .sidebar-icon img {
  filter: brightness(0) invert(1);
}

.sidebar-account {
  margin-top: 20%;
  display: flex;
  flex-direction: row;
  margin-right: 10px;
  justify-content: space-evenly;
}

.sidebar-account .avatar {
  width: 20%;
  height: 20%;
  border-radius: 50%;
}

.sidebar-item:hover {
  background-color: darkslateblue;
  color: white;

  .sidebar-icon {
    filter: brightness(0) invert(1);
  }
}
@media (max-width: 1023px) {
}

.sidebar-account-name {
  font-size: 0.8em;
  color: black;
  margin-top: 5px;
}

.sidebar-account-email {
  color: #777777;
  font-size: 0.7em;
  margin-top: -10px;
}

.icon__logout {
  width: 9%;
  height: 14%;
  margin: 7% 0 0 -10px;
  cursor: pointer;
}
.title_page {
  font-size: 28px;
}
